*,
*::after,
*::before {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    position: relative;
    background-color: rgb(104, 104, 104);
}

h1 {
    text-align: center;
    margin-top: 20px;
    color: aliceblue;
}

form {
    position: fixed;
    bottom: 500px;
    right: 30%;
    left: 30%;
    border-radius: 8px;
    border: 2px solid rgb(42, 42, 207);
    color: #000;
    box-shadow: 0 2px 4px 4px rgba(0, 0, 0, 0.5);
}

.itmes {
    position: fixed;
    top: 50px;
    right: 30%;
    left: 30%;
    border-radius: 8px;
}

.form-group {
    margin-bottom: 0;
}

.completedTodo {
    background-color: darkgray;
    text-decoration: line-through;
}